import React, { ComponentProps } from 'react';
import Helmet from 'react-helmet';
import { useStaticQuery, graphql } from 'gatsby';
import { SiteMetadata } from 'src/models';
import { FluidObject } from 'gatsby-image';

interface Props extends ComponentProps<typeof Helmet> {
  description?: string;
  lang?: string;
  image?: string;
}
interface QueryResult {
  site: {
    siteMetadata: SiteMetadata;
  };
  thumbnail: {
    childImageSharp: {
      fluid: FluidObject;
    };
  };
}

function SEO({ description, lang = 'ko', meta = [], title, image }: Props) {
  const {
    site: { siteMetadata },
    thumbnail,
  }: QueryResult = useStaticQuery(
    graphql`
      query {
        site {
          siteMetadata {
            title
            description
            author
            siteUrl
          }
        }
        thumbnail: file(absolutePath: { regex: "/main/members.jpg/" }) {
          childImageSharp {
            fluid {
              ...GatsbyImageSharpFluid
            }
          }
        }
      }
    `
  );

  const metaTitle = title ?? siteMetadata.title;
  const metaDescription = description ?? siteMetadata.description;
  const metaImage = image ?? thumbnail.childImageSharp.fluid.src;

  const defaultMeta = [
    {
      name: `description`,
      content: metaDescription,
    },
    {
      property: `og:title`,
      content: metaTitle,
    },
    {
      property: `og:description`,
      content: metaDescription,
    },
    {
      property: `og:image`,
      content: metaImage,
    },
    {
      property: `og:type`,
      content: `website`,
    },
    {
      name: `twitter:card`,
      content: `summary`,
    },
    {
      name: `twitter:creator`,
      content: siteMetadata.author,
    },
    {
      name: `twitter:title`,
      content: title,
    },
    {
      name: `twitter:description`,
      content: metaDescription,
    },
    {
      property: `twitter:image`,
      content: metaImage,
    },
    {
      name: 'google-site-verification',
      content: 'B2IRpRzP5pStUAfft7H5A5d-7yR1DYTQiP0M7n1Yw_w',
    },
  ];

  const siteData = {
    '@context': 'https://schema.org',
    '@type': 'WebPage',
    name: siteMetadata.title,
    url: siteMetadata.siteUrl,
    mainEntityOfPage: siteMetadata.siteUrl,
    description: siteMetadata.description,
    author: {
      '@type': 'Person',
      name: siteMetadata.author,
    },
    creator: {
      '@type': 'Person',
      name: siteMetadata.author,
    },
  };

  return (
    <Helmet
      htmlAttributes={{
        lang,
      }}
      defaultTitle={siteMetadata.title}
      title={metaTitle}
      titleTemplate={`%s | ${siteMetadata.title}`}
      meta={[...defaultMeta, ...meta]}
    >
      <link rel="icon" href="/favicon.ico" />
      <script type="application/ld+json">{JSON.stringify(siteData)}</script>
    </Helmet>
  );
}

export default SEO;
