import { useStaticQuery, graphql } from 'gatsby';
import { Member } from 'src/models';

export function useAllMembers() {
  const result: {
    allMembersJson: {
      nodes: Member[];
    };
  } = useStaticQuery(graphql`
    query AllMembersQuery {
      allMembersJson {
        nodes {
          id
          company
          name {
            en
            ko
          }
          profileImg {
            childImageSharp {
              fixed(width: 600, height: 600, quality: 50, cropFocus: CENTER) {
                ...GatsbyImageSharpFixed_noBase64
              }
            }
          }
          role
          rss
          social {
            blog
            github
            linkedin
            unsplash
          }
        }
      }
    }
  `);

  return result.allMembersJson.nodes;
}
