import React, { useMemo, useState, useEffect, ComponentProps } from 'react';
import { Button, makeStyles, Theme } from '@material-ui/core';
import intervalToDuration from 'date-fns/intervalToDuration';
import addMilliseconds from 'date-fns/addMilliseconds';
import Emoji from 'components/Utils/Emoji';

const intervalTime = 1000;

const useStyle = makeStyles<Theme>(theme => ({
  root: {
    position: 'relative',
  },
  description: {
    position: 'absolute',
    left: '50%',
    transform: 'translateX(-50%)',
    marginTop: theme.spacing(1),
    fontSize: theme.typography.body2.fontSize,
    '& strong': {
      fontSize: theme.typography.subtitle1.fontSize,
      marginRight: 2,
      color: theme.palette.primary.main,
    },
  },
}));

interface Props extends ComponentProps<typeof Button> {
  expiry: Date;
}
const CountdownButton = ({ expiry, ...rest }: Props) => {
  const styles = useStyle();
  const [now, setTime] = useState(new Date());

  const count = useMemo(() => {
    return intervalToDuration({
      start: now,
      end: expiry,
    });
  }, [expiry, now]);

  const expired = useMemo(() => {
    return now.getTime() > expiry.getTime();
  }, [expiry, now]);

  useEffect(() => {
    const interval = setInterval(() => {
      setTime(v => addMilliseconds(v, intervalTime));
    }, intervalTime);

    return () => {
      clearInterval(interval);
    };
  }, []);

  return (
    <div className={styles.root}>
      {expired ? (
        <Button fullWidth disabled {...rest}>
          마감되었습니다
        </Button>
      ) : (
        <>
          <Button fullWidth {...rest} />
          <div className={styles.description}>
            <strong>{count.days}</strong>일 <strong>{count.hours}</strong>시간 <strong>{count.minutes}</strong>분{' '}
            <strong>{count.seconds}</strong>초 후에 마감됩니다 <Emoji emoji="🙇‍♂️" name="thanks" />
          </div>
        </>
      )}
    </div>
  );
};

export default CountdownButton;
