import React, { Fragment, useMemo } from 'react';
import { Member, Social } from 'src/models';
import { Card, CardMedia, CardContent, Typography, makeStyles, Theme, createStyles } from '@material-ui/core';
import { SocialIcons } from 'src/constants';
import AnimatedListItem from 'src/components/Animated/AnimatedListItem';

const useStyles = makeStyles<Theme>(theme =>
  createStyles({
    title: {
      fontWeight: 'bold',
    },
    links: {
      margin: '1rem 0 0 0',
      padding: 0,
    },
    linkIcon: {
      listStyle: 'none',
      display: 'inline-block',
      marginRight: '1rem',
      marginBottom: 0,
      '&:last-child': {
        marginRight: 0,
      },
      '& i': {
        color: theme.palette.text.secondary,
        transition: 'transform 0.2s ease-in-out, opacity 0.2s ease-in-out',
        '&:hover': {
          transform: 'translate(0, -5px)',
          opacity: 0.8,
        },
      },
    },
  })
);

interface Props {
  member: Member;
}
const MemberCard = ({ member }: Props) => {
  const socials: Array<[string, string]> = useMemo(
    () => Object.entries(member.social).filter(([, socialUrl]) => Boolean(socialUrl)),
    [member.social]
  );
  const styles = useStyles();

  return (
    <AnimatedListItem>
      <Card>
        <CardMedia component="img" src={member.profileImg.childImageSharp.fixed.src} alt={member.name.ko} />
        <CardContent>
          <Typography component="h3" variant="subtitle1" className={styles.title}>
            {member.name.ko} / {member.name.en}
          </Typography>
          <Typography variant="caption">
            {member.role}
            {member.company ? ` / ${member.company}` : ''}
          </Typography>
          <ul className={styles.links}>
            <Fragment>
              {socials.map(([key, socialUrl]) => (
                <li key={key} className={styles.linkIcon}>
                  <a href={socialUrl} target="_blank" rel="noopener noreferrer">
                    <i className={`${SocialIcons[key as keyof Social]}`} />
                  </a>
                </li>
              ))}
            </Fragment>
          </ul>
        </CardContent>
      </Card>
    </AnimatedListItem>
  );
};

export default MemberCard;
